<template>
    <div class="container">
        <div class="weui-tab">
    <div class="weui-navbar">
      
        <div class="weui-navbar__item" :class = "{'weui-bar__item_on':currentTab == 'inProcess'}"  @click = "change('inProcess')">
            未完成
        </div>
        <div class="weui-navbar__item" :class = "{'weui-bar__item_on':currentTab == 'done'}"  @click = "change('done')">
            已完成
        </div>
        
    </div>
  
</div>

    </div>
</template>
<script>
export default{
  data () {
    return {
      currentTab: 'inProcess'
    }
  },
  methods: {
    change: function (content) {
      this.currentTab = content
    }
  }
}
</script>
<style lang="scss">

</style>


