<template>
  <div id="newOrder">
    <div class="weui-cells__title">表单</div>
<div class="weui-cells weui-cells_form">
    <div class="weui-cell weui-cell_vcode">
        <div class="weui-cell__hd">
            <label class="weui-label">价格</label>
        </div>
        <div class="weui-cell__bd">
            <input class="weui-input" type="tel" placeholder="请输入赏金">
        </div>
        <div class="weui-cell__ft">
            <a href="javascript:;" class="weui-vcode-btn">RMB</a>
        </div>
    </div>
</div>
    <div class="weui-cells__title">物件大小</div>
<div class="weui-cells weui-cells_radio">
    <label class="weui-cell weui-check__label" for="x11">
        <div class="weui-cell__bd">
            <p>小</p>
        </div>
        <div class="weui-cell__ft">
            <input type="radio" class="weui-check" name="radio1" id="x11">
            <span class="weui-icon-checked"></span>
        </div>
    </label>
    <label class="weui-cell weui-check__label" for="x12">

        <div class="weui-cell__bd">
            <p>中</p>
        </div>
        <div class="weui-cell__ft">
            <input type="radio" name="radio1" class="weui-check" id="x12" checked="checked">
            <span class="weui-icon-checked"></span>
        </div>
    </label>
     <label class="weui-cell weui-check__label" for="x13">

        <div class="weui-cell__bd">
            <p>大</p>
        </div>
        <div class="weui-cell__ft">
            <input type="radio" name="radio1" class="weui-check" id="x13" checked="checked">
            <span class="weui-icon-checked"></span>
        </div>
    </label>
</div>
<div class="weui-cells__title">快递信</div>
<div class="weui-cells weui-cells_form">
    <div class="weui-cell">
        <div class="weui-cell__bd">
            <textarea class="weui-textarea" placeholder="请复制你的快递信" rows="3" v-model="sms"></textarea>
            <div class="weui-textarea-counter"><span >{{sms.length}}</span>/200</div>
        </div>
    </div>
</div>
<!--文本域-->
<div class="weui-cells__title">备注</div>
<div class="weui-cells weui-cells_form">
    <div class="weui-cell">
        <div class="weui-cell__bd">
            <textarea class="weui-textarea" placeholder="可以详细描述大小，以便快速取件" rows="2" v-model="note"></textarea>
            <div class="weui-textarea-counter"><span>{{note.length}}</span>/50</div>
        </div>
    </div>
</div>
<div class="button">
  <a href="javascript:;" class="weui-btn weui-btn_primary">提交</a>
<a href="javascript:;" class="weui-btn weui-btn_warn">取消</a>
</div>
  </div>
</template>
<script>
export default {
  name: 'Order',
  data () {
    return {
      sms: '',
      note: ''
    }
  }
}
</script>


<style scoped lang="scss">
.button{
  margin-top:90px;
}
</style>



