<script>
export default {
  name: 'newItem'
}
</script>


<style lang="scss" scoped>
  $size: 45px;
@font-face {
  font-family: 'iconfont';  /* project id："170505" */
  src: url('//at.alicdn.com/t/font_lw8ry6ju8c0g4x6r.eot');
  src: url('//at.alicdn.com/t/font_lw8ry6ju8c0g4x6r.eot') format('embedded-opentype'),
  url('//at.alicdn.com/t/font_lw8ry6ju8c0g4x6r.woff') format('woff'),
  url('//at.alicdn.com/t/font_lw8ry6ju8c0g4x6r.ttf') format('truetype'),
  url('//at.alicdn.com/t/font_lw8ry6ju8c0g4x6r.svg#iconfont') format('svg');
}
.iconfont{
  font-family: iconfont;
   font-size:2.0rem;font-style:normal;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: 0.2px;
    -moz-osx-font-smoothing: grayscale;
    
    }

#newItem{
  
  position: fixed;
  right:25px;
  bottom:85px;
  background-color: #89db89;
  width:$size;
  height:$size;
  border-radius:100px;
  box-shadow: 1px 1px 6px 1px rgba(6,6,6,.3);
  .add{
   position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.black{
  color:black;
}
}

</style>



