<template>
<div class="packageListItem" >
 <div class="weui-panel weui-panel_access">
    
    <div class="weui-panel__bd">
        <a href="javascript:void(0);" class="weui-media-box weui-media-box_appmsg">
            <div class="weui-media-box__hd">
                <span class="weui-media-box__thumb" :class="genderClass"  >{{price}}</span>
            </div>
            <div class="weui-media-box__bd">
                <h4 class="weui-media-box__title">{{location}}</h4>
                <p class="weui-media-box__desc">{{note}}</p>
                <ul class="weui-media-box__info">
                <li class="weui-media-box__info__meta">来源</li>
                <li class="weui-media-box__info__meta">时间</li>
                <li class="weui-media-box__info__meta weui-media-box__info__meta_extra">包裹：{{size}}</li>
            </ul>
            </div>
        </a>
        
    </div>  
</div>
<i class="iconfont next">&#xe659;</i>
</div>
</template>
<script>
export default{
  computed: {
    genderClass: function () {
      return this.gender === '女' ? 'packageListItemWomen' : 'packageListItemMan'
    }
  },
  props: {
    price: {
      type: String,
      require: true
    },
    time: {
      type: String,
      default: '18:00--21:00'
    },
    location: {
      require: true
    },
    gender: String,
    size: String,
    note: {
      type: String,
      default: '暂无描述'
    }
  }
}
</script>
<style lang="scss" scoped>
@font-face {
  font-family: 'iconfont';  /* project id："170505" */
  src: url('//at.alicdn.com/t/font_p9vy3bkdvfx8yqfr.eot');
  src: url('//at.alicdn.com/t/font_p9vy3bkdvfx8yqfr.eot') format('embedded-opentype'),
  url('//at.alicdn.com/t/font_p9vy3bkdvfx8yqfr.woff') format('woff'),
  url('//at.alicdn.com/t/font_p9vy3bkdvfx8yqfr.ttf') format('truetype'),
  url('//at.alicdn.com/t/font_p9vy3bkdvfx8yqfr.svg#iconfont') format('svg');
}
.iconfont{
    font-family:"iconfont" !important;
    font-size:rem;font-style:normal;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: 0.2px;
    -moz-osx-font-smoothing: grayscale;}

 .packageListItemWomen{
     color:#FF9999;
 }
 .packageListItemMan{
     color:#89db89;
 }
 .packageListItem{
   position:relative;
 .next{
   position:absolute;
   z-index:9;
   top: 50%;
   color:gray;
  transform: translateY(-50%);
   right:0;

 }
 }
</style>


